  /* Fullscreen background */
.register-page {
  height: 100vh;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(195deg, #fdfdfd 0%, #3e84cf 90%);
  position: relative;
  overflow: hidden;
}

/* Glassmorphism effect */
.register-form {
  background: rgba(255, 255, 255, 0.15);
  backdrop-filter: blur(10px);
  padding: 40px;
  border-radius: 15px;
  box-shadow: 0px 10px 30px rgba(240, 174, 7, 0.2);
  text-align: center;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  width: 320px;
}

.register-form:hover {
  transform: scale(1.03);
  box-shadow: 0px 15px 40px rgba(0, 0, 0, 0.25);
}

/* Heading */
.register-form h1 {
  font-size: 30px;
  font-weight: bold;
  text-shadow: 1px 1px 5px rgba(0, 0, 0, 0.2);
  margin-bottom: 20px;
  text-align: center;
  font-weight: bold;
  color: #344265;
  font-family: 'Times New Roman', Times, serif;
}

/* Input fields */
.register-form .ant-form-item {
  margin-bottom: 20px;
}

.register-form .ant-form-item-label label {
  color: #100d0d !important;
  font-size: 18px;
  font-family: 'Times New Roman', Times, serif;
}

/* Input box styling */
.register-form .ant-input {
  padding: 12px;
  border-radius: 8px;
  border: 1px solid rgba(255, 255, 255, 0.4);
  background: rgba(255, 255, 255, 0.2);
  color: #fff;
  transition: all 0.3s ease;
}

.register-form .ant-input:focus,
.register-form .ant-input:hover {
  background: rgba(255, 255, 255, 0.3);
  border: 1px solid #ffffff;
}

/* Buttons */
.register-form button {
  width: 65%;
  padding: 10px;
  border-radius: 15px;
  font-size: 16px;
  font-weight: bold;
  background: #ff7f50;
  transition: all 0.3s ease;
  cursor: pointer;
  background-color: #45557e;
  color: white;
  outline: none;
}

.register-form button:hover {
  background: #1abc0e;
  transform: scale(1.05);
}

/* Links */
.register-form a {
  display: block;
  color: #05080a !important;
  font-size: 18px;
  margin-top: 10px;
  transition: color 0.3s ease;
  font-family: 'Times New Roman', Times, serif;
}

.register-form a:hover {
  color: #0a0a0a !important;
}

/* Responsive Design */
@media (max-width: 400px) {
  .register-form {
      width: 90%;
  }
}


/* Fullscreen Background */
.register-page {
  height: 100vh;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(195deg, #fdfdfd 0%, #3e84cf 90%);
}

/* Card Styling */
.register-card {
  background: rgba(255, 255, 255, 0.15);
  backdrop-filter: blur(10px);
  padding: 40px;
  border-radius: 15px;
  box-shadow: 0px 10px 30px rgba(240, 174, 7, 0.2);
  text-align: center;
  width: 350px;
  transition: all 0.3s ease-in-out;
}

.register-card:hover {
  transform: scale(1.03);
  box-shadow: 0px 15px 40px rgba(0, 0, 0, 0.25);
}

/* Heading */
.register-card h1 {
  font-size: 28px;
  font-weight: bold;
  color: #344265;
  margin-bottom: 20px;
}

/* Input Fields */
.ant-form-item {
  margin-bottom: 15px;
}

.ant-input {
  padding: 12px;
  border-radius: 8px;
  background: rgba(255, 255, 255, 0.2);
  border: 1px solid rgba(255, 255, 255, 0.4);
  color: #000;
  transition: 0.3s;
}

.ant-input:focus,
.ant-input:hover {
  background: rgba(255, 255, 255, 0.3);
  border: 1px solid #ffffff;
}

/* Buttons */
.register-btn {
  width: 100%;
  padding: 10px;
  border-radius: 15px;
  font-size: 16px;
  font-weight: bold;
  background-color: #45557e;
  color: white;
  transition: all 0.3s ease-in-out;
  cursor: pointer;
  border: none;
}

.register-btn:hover {
  background-color: #1abc0e;
  transform: scale(1.05);
}

/* Form Footer */
.form-footer {
  display: flex;
  flex-direction: column;
  gap: 10px;
  text-align: center;
}

.form-footer a {
  color: #05080a !important;
  font-size: 16px;
  transition: color 0.3s ease;
}

.form-footer a:hover {
  color: #0a0a0a !important;
}

/* Responsive */
@media (max-width: 400px) {
  .register-card {
    width: 90%;
  }
}
