/* üåü Table Container - Adds spacing & shadow */
.ant-table {
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0px 4px 10px rgba(53, 53, 49, 0.692);
  background: #ffffff;
}


/* üèÜ Header Styling */
.ant-table-thead > tr > th {
  background-color: #eea744 !important;
  font-weight: bold;
  text-align: center;
  padding: 14px;
  font-size: 16px;
  color: #333;
  border-bottom: 2px solid #ddd;
}

/* üè∑Ô∏è Table Row Styling */
.ant-table-tbody > tr {
  transition: background 0.3s ease-in-out;
}

.ant-table-tbody > tr:hover {
  background: #f1f3f5; /* Subtle row highlight on hover */
}

/* üí∞ Amount Column: Green for Income, Red for Expense */
.table-amount {
  font-weight: bold;
  font-size: 14px;
}

.table-amount.expense {
  color: #e74c3c; /* Red for expenses */
}

.table-amount.income {
  color: #28a745; /* Green for income */
}

/* üóìÔ∏è Date Column Styling */
.table-date {
  color: #000307;
  font-weight: bold;
  font-size: 14px;
}

/* üîñ Category Styling */
.table-category {
  /*font-style: italic;*/
  font-weight: 500;
  color: #6c757d;
  text-transform: capitalize;
}

/* üîÑ Type Column - Capitalized */
.table-type {
  text-transform: capitalize;
  font-weight: 500;
}

/* üìÑ Reference Styling */
.table-reference {
  color: #888;
  text-transform: capitalize;
  font-size: 14px;
}

/* üé® Actions Column - Styling Edit & Delete Icons */
.table-actions {
  display: flex;
  /*justify-content: center;
  align-items: center;*/
  gap: 20px; /* Adds spacing between icons */
}

/* ‚úèÔ∏è Edit Icon */
.edit-icon {
  color: #f39c12;
  cursor: pointer;
  transition: transform 0.2s ease-in-out, color 0.3s;
}

.edit-icon:hover {
  transform: scale(1.2);
  color: #e67e22;
}

/* ‚ùå Delete Icon */
.delete-icon {
  color: #dc3545;
  cursor: pointer;
  transition: transform 0.2s ease-in-out, color 0.3s;
}

.delete-icon:hover {
  transform: scale(1.2);
  color: #c82333;
}


/* Ensure the page takes full height */
html, body, #root {
  height: 100%;
  margin: 0;
  display: flex;
  flex-direction: column;
}

/* Make the content section grow to push footer down */
.main-content {
  flex: 1; /* This makes sure the main content expands and pushes the footer down */
}

/* Sticky Footer */
.footer {
  background-color: #343a40;
  color: white;
  text-align: center;
  padding: 10px;
  position: relative;
  width: 100%;
}

/* Style for name */
.footer-name {
  text-decoration: underline;
  cursor: pointer;
  position: relative;
  display: inline-block;
}

/* Tooltip styling */
.tooltip {
  position: absolute;
  background-color: #222;
  color: #fff;
  padding: 8px;
  border-radius: 5px;
  bottom: 25px; /* Adjusts position above the name */
  left: 50%;
  transform: translateX(-50%);
  white-space: nowrap;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
  z-index: 1000;
  font-size: 14px;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease-in-out;
}

/* Show tooltip when hovering */
.footer-name:hover .tooltip {
  opacity: 1;
  visibility: visible;
}
