.profile-page {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #f4f4f4;
  }
  
  .profile-card {
    width: 400px;
    text-align: center;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    background-color: #fff;
  }
  
  .profile-title {
    font-size: 22px;
    font-weight: bold;
    margin-bottom: 15px;
  }
  
  .profile-icon {
    font-size: 50px;
    color: #1890ff;
    margin-bottom: 10px;
  }
  
  .profile-info {
    font-size: 16px;
    margin: 10px 0;
  }
  
  .icon {
    margin-right: 5px;
    color: #6f4e37;
  }
  
  .password-input {
    width: 100%;
    margin-left: 5px;
  }
  
  .password {
    font-weight: bold;
    color: #555;
  }
  
  .button-group {
    display: flex;
    justify-content: center;
    gap: 10px;
  }
  
  .edit-password-btn {
    margin-top: 15px;
    background-color: #1890ff;
    color: white;
    border: none;
  }
  
  .edit-password-btn:hover {
    background-color: #1ad221;
    color: rgb(8, 8, 8);
  }
  
  .cancel-btn {
    background-color: #f44336;
    color: white;
  }
  
  .cancel-btn:hover {
    background-color: #d32f2f;
  }
  