  /* Fullscreen container */
.login-page {
  height: 100vh;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(195deg, #fdfdfd 0%, #3e84cf 90%);
  position: relative;
  overflow: hidden;
}

/* Glassmorphism effect */
.login-form {
  background: rgba(255, 255, 255, 0.15);
  backdrop-filter: blur(10px);
  padding: 40px;
  border-radius: 15px;
  box-shadow: 0px 10px 30px rgba(240, 174, 7, 0.2);
  text-align: center;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  width: 320px;
}

.login-form:hover {
  transform: scale(1.03);
  box-shadow: 0px 15px 40px rgba(0, 0, 0, 0.25);
}

/* Heading */
.login-form h1 {
  font-size: 30px;
  font-weight: bold;
  text-shadow: 1px 1px 5px rgba(0, 0, 0, 0.2);
  margin-bottom: 20px;
  text-align: center;
    font-weight: bold;
    margin: 30px;
    color: #344265;
    font-family:'Times New Roman', Times, serif;
}
h1 {
  font-size: 40px;
  font-weight: bold;
  text-shadow: 1px 1px 5px rgba(0, 0, 0, 0.2);
  font-weight: bold;
  color: #344265;
  font-family:'Times New Roman', Times, serif;
}

/* Input fields */
.login-form .ant-form-item {
  margin-bottom: 20px;
}

.login-form .ant-form-item-label label {
  color: #100d0d !important;
  font-size: 18px;
  font-family:'Times New Roman', Times, serif;
}

/* Input box styling */
.login-form .ant-input {
  padding: 12px;
  border-radius: 8px;
  border: 1px solid rgba(255, 255, 255, 0.4);
  background: rgba(255, 255, 255, 0.2);
  color: #fff;
  transition: all 0.3s ease;
}

.login-form .ant-input:focus,
.login-form .ant-input:hover {
  background: rgba(255, 255, 255, 0.3);
  border: 1px solid #ffffff;
}

/* Buttons */
.login-form button {
  width: 65%;
  
  padding: 10px;
  border-radius: 15px;
  font-size: 16px;
  font-weight: bold;
  background: #ff7f50;
  transition: all 0.3s ease;
  cursor: pointer;
  background-color: #45557e;
  color: white;
  outline: none;
}

.login-form button:hover {
  background: #1abc0e;
  transform: scale(1.05);
}

/* Links */
.login-form a {
  display: block;
  color: #05080a !important;
  font-size: 18px;
  margin-top: 10px;
  transition: color 0.3s ease;
  font-family:'Times New Roman', Times, serif;
}

.login-form a:hover {
  color: #0a0a0a !important;
}

/* Responsive Design */
@media (max-width: 400px) {
  .login-form {
      width: 90%;
  }
}
