/* üõ†Ô∏è Adjustments for Filters Section */
.filters {
    border-radius: 8px;
    margin-bottom: 20px;
    background: #f8f9fa;
  }
  
  /* üéõÔ∏è Select Dropdown Styling */
  .filters select,
  .filters .ant-select-selector {
    padding: 6px 12px !important;
    border-radius: 5px !important;
  }
  
  /* üìä Analytics & Table View Switch */
  .switch-icons {
    display: flex;
    align-items: center;
    gap: 12px;
  }
  
  .active-icon {
    font-size: 22px;
    color: #007bff;
  }
  
  .inactive-icon {
    font-size: 20px;
    color: gray;
  }
  
  /* üíæ Modal Styling */
  .ant-modal {
    border-radius: 10px !important;
    padding: 20px;
  }
  
  .ant-modal-title {
    font-size: 18px;
    font-weight: bold;
    text-align: center;
  }
  
  /* üìã Form Styling */
  .ant-form-item {
    margin-bottom: 14px !important;
  }
  
  .ant-input,
  .ant-select-selector {
    border-radius: 5px !important;
    padding: 8px !important;
  }
  
  /* üÜï Add Button */
  .btn-primary {
    background-color: #007bff !important;
    color: white;
    border-radius: 5px;
    padding: 8px 14px;
    transition: 0.3s;
  }
  
  .btn-primary:hover {
    background-color: #0056b3 !important;
  }
  
  /* üè† Home Page Content Spacing */
  .content {
    margin-top: 20px;
    padding: 20px;
    border-radius: 8px;
    background: #fff;
  }

  /* üåü Modal Form Styling */
.ant-modal {
    border-radius: 12px !important;
    padding: 25px;
  }
  
  /* üè∑Ô∏è Form Fields Styling */
  .ant-form-item {
    margin-bottom: 16px !important;
  }
  
  .ant-form-item-label label {
    font-weight: 600;
    font-size: 14px;
    color: #333;
  }
  
  /* üìù Input Fields */
  .ant-input,
  .ant-select-selector {
    border-radius: 8px !important;
    padding: 10px !important;
    border: 1px solid #d9d9d9;
    font-size: 14px;
  }
  
  /* üìÜ Date Picker Styling */
  .ant-picker {
    border-radius: 8px !important;
    padding: 8px 12px !important;
  }
  
  /* ‚úÖ Save Button Styling */
  .btn-primary {
    background-color: #007bff !important;
    color: white;
    border-radius: 6px;
    padding: 10px 20px;
    font-weight: bold;
    transition: all 0.3s;
  }
  
  .btn-primary:hover {
    background-color: #0056b3 !important;
    transform: scale(1.05);
  }
  