  /* Navbar Container */
.navbar {
  background: linear-gradient(135deg, #6a89b8 0%, #1963b3);
  backdrop-filter: blur(10px); /* Adds a blur effect for a modern look */
  padding: 10px 20px; /* Adjusted padding */
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1); /* Soft shadow */
  transition: all 0.3s ease-in-out;
}

/* Navbar Brand */
.navbar .navbar-brand {
  color: #0e0e0d !important;
  font-weight: bold;
  font-size: 1.7rem; /* Slightly larger for better visibility */
  letter-spacing: 1px;
  transition: all 0.3s ease-in-out;
}

.navbar .navbar-brand:hover {
  color: #f8f8f8 !important; /* Darker shade on hover */
  transform: scale(1.05);
}

/* Navbar Links */
.navbar .nav-link {
  font-size: 1.2rem;
  color: #ffffff !important;
  font-weight: 500;
  margin: 0 10px;
  transition: all 0.3s ease-in-out;
}

.navbar .nav-link:hover {
  color: #f4e1c7 !important; /* Light beige color on hover */
  transform: translateY(-2px);
}

/* Navbar Username */
.navbar h6 {
  margin-top: 0px;
  margin-right: 20px;
  font-size: 1.1rem;
  color: #fff;
  font-weight: 500;
}

/* Responsive Navbar */
@media (max-width: 768px) {
  .navbar .nav-link {
    font-size: 1rem; /* Smaller font size for smaller screens */
  }
  .navbar .navbar-brand {
    font-size: 1.4rem;
  }
  .navbar h6 {
    font-size: 1rem;
    margin-right: 10px;
  }
}

.profile-btn {
  background: none;
  border: none;
  font-size: 1.2rem;
  font-weight: 500;
  color: #060606;
  cursor: pointer;
  transition: 0.3s;
}

.profile-btn:hover {
  color: #eceae8;
  transform: scale(1.05);
}


/* Guide Button Styles */
.guide-btn {
  background: linear-gradient(135deg, #1890ff, #722ed1);
  color: white;
  border: none;
  font-weight: bold;
  padding: 8px 15px;
  border-radius: 20px;
  transition: all 0.3s ease-in-out;
}

.guide-btn:hover {
  background: linear-gradient(135deg, #722ed1, #1890ff);
  transform: scale(1.1);
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2);
}

/* Guide Modal Styling */
.ant-modal-title {
  font-size: 20px !important;
  font-weight: bold;
  color: #1890ff !important;
  text-align: center;
}

.ant-modal-content {
  background: #f9f9f9;
  border-radius: 12px;
  padding: 20px;
}

.ant-modal-footer .ant-btn-primary {
  background: linear-gradient(135deg, #1890ff, #722ed1);
  border: none;
  font-weight: bold;
  border-radius: 10px;
}

.ant-modal-footer .ant-btn-primary:hover {
  background: linear-gradient(135deg, #722ed1, #1890ff);
  transform: scale(1.05);
}

/* Guide Step Styling */
.guide-step {
  font-size: 16px;
  color: #555;
  display: flex;
  align-items: center;
  margin: 10px 0;
}

.guide-step i {
  font-size: 18px;
  margin-right: 10px;
  color: #1890ff;
}

.guide-text {
  font-weight: bold;
  color: #333;
}



